<template>
  <div class="welfare-wrapper" v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
    <div class="welfare-center">
      <div v-show="leftData.length > 0" v-for="data in leftData">
          <v-video class="video-wrap" ref="v-video" :data="data"></v-video>
          <!-- <div class="desc">
            <span class="title" v-text="data.title"></span>
            <span class= "author">by <b>{{ data.author }} </b></span>
            <v-vote class="like" :hasVoted="true" :callBack="onVoteClick(data.videoId, )"></v-vote>
          </div> -->

        <!-- <div class="divone">
          <div class="ceng">
            <div class="bg"></div>
            <div class="nr">
              <span>by <b>{{ data.author }} </b></span>
            </div>
          </div>
        </div> -->
        <!--<v-mimg  ref="v-img" v-if="showFlag" :imgUrl="data.img"></v-mimg>-->
      </div>
    </div>
    <div class="welfare-center">
      <div v-show="rightData.length > 0" v-for="data in rightData">
        <v-video class="video-wrap" ref="v-video" :data="data"></v-video>
        <!-- <div class="desc">
          <span class="title" v-text="data.title"></span>
          <span class= "author">by <b>{{ data.author }} </b></span>
          <v-vote class="like" :hasVoted="data.videoId" :callBack="onVoteClick()"></v-vote>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
  import vVideo from '../lazyloadimg/video.vue';
  import vDetails from '../details/details.vue';
  import videoSdk from '../lazyloadImg/video_sdk.js';

  export default {
    data() {
      return {
        leftData: [],
        rightData: [],
        busy: false,
        pageNum: 6,
        detailsData: {},
        time: '',
        showFlag: true
      };
    },
    components: {
      vVideo,
      vDetails
    },
    created() {
      videoSdk.serverUrl = 'http://service.fonsview.com:8780/v1';
      videoSdk.updateVideoList('', 0, this.pageNum, this.getTopVideoList.bind(this));
    },
    mounted() {

    },
    methods: {
      getTopVideoList(videos) {
        let newList = [];
        console.log('getTopVideoList');
        for (var e of videos) {
          let tmp = {};
          tmp.videoId = e.VideoId;
          tmp.img = e.SnapUrl;
          tmp.title = e.VideoDesc;
          tmp.author = e.UserName;
          tmp.videoUrl = e.VideoUrl;
          tmp.voteCount = e.VoteCounts;
          tmp.orientation = e.ScreenOrientation;

          newList.push(tmp);
          console.log(tmp);
        }
        let left = newList.filter((data, i) => {
          // console.log('left' + i);
         // console.log('left' + newList[i].videoUrl);
          return i % 2 === 1;
        });
        let right = newList.filter((data, i) => {
          // console.log('right' + i);
          return i % 2 !== 1;
        });

       /* console.log('left length:' + left.length);
        console.log('right length:' + right.length);

        for (var tmp of left) {
            console.log('left' + tmp.videoUrl);
        }
        for (tmp of right) {
          console.log('right' + tmp.videoUrl);
        } */
        this.leftData = this.leftData.concat(left);
        this.rightData = this.rightData.concat(right);
      },

      getNextVideoList(videos) {
        let newList = [];
        console.log('getNextVideoList');
        for (var e of videos) {
          let tmp = {};

          tmp.videoId = e.VideoId;
          tmp.img = e.SnapUrl;
          tmp.title = e.VideoDesc;
          tmp.author = e.UserName;
          tmp.videoUrl = e.VideoUrl;
          tmp.voteCount = e.VoteCounts;
          tmp.orientation = e.ScreenOrientation;

          newList.push(tmp);

          console.log(tmp);
        }

        let left = newList.filter((data, i) => {
          return i % 2 === 1;
        });
        let right = newList.filter((data, i) => {
          return i % 2 !== 1;
        });
        this.leftData = this.leftData.concat(left);
        this.rightData = this.rightData.concat(right);
      },

      loadTop() {
        // this.$store.commit('UPDATE_LOADING', true);
        /* this.page = 4;
        this.$http.get(`https://gank.io/api/data//10/${this.page}`).then((response) => {
          let left = response.body.results.filter((data, i) => {
            return (i + 1) % 2 === 1;
          });
          let right = response.body.results.filter((data, i) => {
            return (i + 1) % 2 !== 1;
          });
          this.leftData = this.leftData.concat(left);
          this.rightData = this.rightData.concat(right);
          this.busy = false;
        // $nextTick() dom 
          this.$nextTick(() => {
            this.$store.commit('UPDATE_LOADING', false);
          });
        }); */

        videoSdk.updateVideoList('', 0, this.pageNum, this.getTopVideoList.bind(this));
        this.busy = false;
        // $nextTick() dom 
        this.$nextTick(() => {
          // this.$store.commit('UPDATE_LOADING', false);
        });
      },
      loadMore() {
        if ((this.leftData.length + this.rightData.length) === 0) {
          return;
        }
        this.busy = true;
        // this.$store.commit('UPDATE_LOADING', true);
        console.log('loadMore ' + this.leftData.length + this.rightData.length);
        videoSdk.updateVideoList('', this.leftData.length + this.rightData.length, this.pageNum, this.getNextVideoList.bind(this));
        this.busy = false;

        // this.loadTop();
        this.pageNum;
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "welfare.styl";
</style>



